import $ from 'jquery'

export default {
  components: {

  },
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    btnclick () {
      if (this.username === 'root' && this.password === 'root') {
        $('audio#cake-music')[0].play()
        let url = '/cake'
        this.$router.push({ path: url })
      } else {
        alert('用户名或密码不正确！')
      }
    }
  }
}



